<template>
  <form @submit.prevent="AddFactura" >
            <div>

                 <input
                    type='date'
                    name='fecha'
                    class='form-control mt-3'
                    placeholder='Fecha'
                    autoComplete='off'
                    v-model="factura.fecha"
                /> 
            
                 <input
                    type='number'
                    name='monto'
                    class='form-control mt-3'
                    placeholder='Monto'
                    autoComplete='off'
                    v-model="factura.monto"

                />
                

                <button 
                class='btn btn-primary form-control  mt-2'
                type='submit'  
                :disabled="getIsLoading"
                >
                Guardar

                </button>

            </div>
        </form>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
   
    data() {
        return{
            factura:{}
        }
    },
    computed:{
        ...mapGetters('facturar',['getId','getIsLoading'])
    },
    methods:{
        ...mapActions('facturar',['addFactura']),
        async AddFactura(){

            
            var facturaAdd = {
                fecha: this.factura.fecha,
                monto: this.factura.monto,
                numero: this.getId
            }
            await this.addFactura(facturaAdd)
        }
    },
}
</script>

